@import url("https://fonts.googleapis.com/css?family=Quicksand");

* {
  font-family: Quicksand;
}

/* div {
  border: .5px dotted black;
} */

.box {
  border: lightgray 1px solid;
  margin: 1%;
  padding: 1%;
  float: center;
}

.note_list {
  padding: 20px;
  background-color: white;
  border-radius: 5px;
  margin: 2%;
}

.notes {
  display: inline-block;
  padding: 3px 15px;

  margin-right: 5px;
  margin-bottom: 5px;

  background-color: #eef7cb;
  border: 1px solid grey;
  border-radius: 20px;
}

input {
  margin-bottom: 10px;
  height: 20px;
  width: 200px;
}


/* ------------------------------------------------------------- */
/* HOME PAGE: SIDEBAR */

.side {
  text-align: center;
  margin: 0;
  float: center;
}

.sidebar {
  display: inline;
  width: 20%;
  float: left;
  margin: 0;
}

.username {
  font-size: 100%;
}

.title_logo {
  width: 100%;
  display: inline-block;
  align-items: center;
  padding: 2%;
  background-color: #283b32;
}

.notes_home {
  background-color: #dad7cd;
  display: inline;
  width: 80%;
  height: 100%;
  float: right;
}

/* ------------------------------------------------------------- */
/* HOME PAGE: TITLE SECTION */

.title_header {
  background-color: #3a5a40;
  display: flex;
  padding: 1%;
  font-family: Quicksand;
  text-align: left;
  color: white;
}

/* HOME PAGE: TITLE SECTION */
.title_name {
  display: inline;
  float: left;
}

/* HOME PAGE: SEARCH BAR */
.search_box {
  padding: 2%;
  display: inline;
  float: right;
  width: 70%;
  align-items: center;
  align-self: right;
}

.searchbar {
  display: inline;
  width: 80%;
  resize: none;
  /* float: right; */
}

.search_btn {
  /* float: right; */
  margin: 1%;
}



/* ------------------------------------------------------------- */
/* HOME PAGE: SUBHEADER SECTION */

.subheader {
  border: none;
  margin: 0;
  padding: 2%;
  background-color: #588157;
}

.note_editor_btn {
  margin: 0 .5% 0 .5%;
  padding: .5%;
}

.side_subheader_btns {
  display: block;
  width: 100%;
  border: none;
  margin: 0;
  padding: 2%;
} 

.side_user_btns_box {
  margin: 10px;
  padding: 10px;
}

.error {
  font-family: Quicksand;
  background-color: lightpink;
}

.btns_list {
  margin: 15px 25%;
}

.btn {
  margin: 0px 5px;
}

.save {
  margin-left: 0px;
  padding: 3px 10px;
  font-weight: bold;
  float: right;
}

.logout {
  margin: 4%;
  padding: 5px 10px;
  font-weight: bold;
  float: left;
}

.singular_note {
  display: inline-block;
  width: 30%;
  height: 30%;
  margin: 5px;
}


/* ------------------------------------------------------------- */
/* EDIT PAGE: EDIT SECTION */


.title_input {
  /* border: none; */
  /* background-color: #dad7cd; */
  font-size: larger;
  width: 50%;
  margin: 10px 0 10px 0;
  padding: 10px;
  resize: none;
}

.edit_page_btns {
  display: block;
  margin: 20px 0 20px 20px;
  padding: 5px;
  float: right;
}

.note_input {
  width: 98%;
  padding: 10px;
  resize: none;
}

.darken {
  background-color: rgb(73, 73, 73);
}

.red {
  background-color: rgb(255, 0, 0);
}

.green {
  background-color: rgb(0, 255, 4);
}

/* ------------------------------------------------------------- */
/* MAP EDIT PAGE: EDIT SECTION */

.map {
  display: flex;
  width: 100%;
  /* padding: 10%; */

  background-size: cover;
}

table {
  width: 100%;
}

td {
  height: auto;
}

tr {
  height: 47px;
}



/* ------------------------------------------------------------- */
/* COMPONENT: NOTE */

.title_section {
  display: flex;
  background-color: #d9d9d9;
}

.title {
  display: flex;
  align-items: center;
  font-size: 150%;
  padding: 10px;
  width: 70%;
}

.pin_btn {
  width: 15%;
}

.body_section {
  background-color: #eeeaea;
}

.textbox {
  resize: none;
  border: none;
  padding: 15px;
  margin: 0;
  width: 90%;
  height: 150px;
  overflow-wrap: break-word;
  overflow: hidden;
}

.trash_btn {
  float: right;
}


/* ------------------------------------------------------------- */
/* COMPONENT: SETTING LIST */

.dropdown {
  display: block;
  position: absolute;
  top: 100%;
  left: 0px;
  z-index: 1;
  width: 100%;
}

.dropdown_div {
  position: relative;
}

.dropdownItem {
  display: flex;
  flex-grow: 1;
  border: 0.5px solid grey;
  height: 25px;

}